import React, { useState } from 'react';
// import bannerLogin from '@/assets/singin-banner.png';
import { ReactComponent as AppleSocial } from '@/assets/apple-social.svg';
import { ReactComponent as FacebookSocial } from '@/assets/facebook-social.svg';
import { ReactComponent as GoogleSocial } from '@/assets/google-social.svg';
import { Box, FormHelperText, Grid, IconButton, Paper, Typography } from '@mui/material';
import useMediaQuery from '@/services/useMediaQuery';
import { SignUpRequest } from '@/@types';
import webAuth, { config } from '@/config/auth0.config';
import SignUpForm from './components/SignUpForm';
import { FormProvider, useForm } from 'react-hook-form';

interface SignUpPageProps {
  toLogin: () => void;
  onSuccess: () => void;
}

function SignUpPage({ toLogin, onSuccess }: SignUpPageProps) {
  const { isMobile } = useMediaQuery();

  const [error, setError] = useState<string>();

  const [isLoading, setIsLoading] = useState(false);

  const handleSignUp = (data: SignUpRequest) => {
    const databaseConnection = 'Username-Password-Authentication';
    setIsLoading(true);
    try {
      webAuth?.signup(
        {
          connection: databaseConnection,
          email: data.email,
          password: data.password,
        },
        error => {
          setIsLoading(false);
          if (error) handleError(error);
          else {
            onSuccess();
          }
        }
      );
    } catch (error) {
      handleError('Something went wrong. Please try again later!');
      setIsLoading(false);
    }
  };

  const handleError = (error?: auth0.Auth0Error | string) => {
    if (typeof error === 'string') {
      setError(error);
    } else {
      setError(error?.description || error?.policy);
    }
  };

  const socialSignUp = (social: 'facebook' | 'google' | 'apple') => {
    let connection;
    switch (social) {
      case 'google':
        connection = 'google-oauth2';
        break;
      case 'facebook':
        connection = 'facebook';
        break;
      case 'apple':
        connection = 'apple';
        break;
      default:
        break;
    }

    webAuth?.authorize({
      connection,
      domain: config.auth0Domain,
      clientID: config.clientID,
      redirectUri: config.callbackURL,
      responseType: 'token id_token',
    });
  };

  const form = useForm<SignUpRequest>({
    defaultValues: {
      email: '',
      password: '',
      agreement: true,
    },
  });

  const isAllowSignUp = form.watch('agreement');

  return (
    <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
      <Paper
        elevation={3}
        sx={{
          borderRadius: isMobile ? 0 : 4,
          width: '100%',
          maxWidth: 496,
          height: isMobile ? '100vh' : 'unset',
          display: isMobile ? 'flex' : 'block',
          flexDirection: 'column',
          gap: isMobile ? 20 : 0,
        }}
      >
        <FormProvider {...form}>
          <Box>
            <Box
              sx={{
                maxWidth: '100%',
                height: 'auto',
                backgroundColor: '#DFB424',
                borderRadius: 4,
                borderTopLeftRadius: isMobile ? 0 : 16,
                borderTopRightRadius: isMobile ? 0 : 16,
              }}
            >
              <img
                src="https://raw.githubusercontent.com/viktor1910/login-form/main/public/signup-banner.png"
                alt="login-banner"
                style={{
                  width: '100%',
                  height: 'auto',
                  display: 'block',
                  borderTopLeftRadius: isMobile ? 0 : 16,
                  borderTopRightRadius: isMobile ? 0 : 16,
                }}
              />
            </Box>
            <Box
              sx={{
                px: isMobile ? 4 : 10,
                pt: isMobile ? 6 : 10,
                pb: isMobile ? 6 : 10,
              }}
            >
              {error && (
                <Box pb={10}>
                  <FormHelperText error>{error}</FormHelperText>
                </Box>
              )}
              <SignUpForm onSignUp={handleSignUp} toLogin={toLogin} isLoading={isLoading} />
            </Box>
          </Box>

          <Box marginBottom={4}>
            <Typography variant="body2" color="#7D848D" textAlign="center">
              Or sign up with
            </Typography>
            <Grid container justifyContent="space-between" width={200} mx="auto" wrap="nowrap" mt={4}>
              {isMobile && (
                <Grid item pt={0}>
                  <IconButton
                    onClick={() => socialSignUp('facebook')}
                    disabled={!isAllowSignUp}
                    sx={{
                      padding: 0,
                    }}
                  >
                    <FacebookSocial />
                  </IconButton>
                </Grid>
              )}

              <Grid item pt={0}>
                <IconButton
                  onClick={() => socialSignUp('google')}
                  disabled={!isAllowSignUp}
                  sx={{
                    padding: 0,
                  }}
                >
                  <GoogleSocial />
                </IconButton>
              </Grid>
              {isMobile && (
                <Grid item pt={0}>
                  <IconButton
                    onClick={() => socialSignUp('apple')}
                    disabled={!isAllowSignUp}
                    sx={{
                      padding: 0,
                    }}
                  >
                    <AppleSocial />
                  </IconButton>
                </Grid>
              )}
            </Grid>
          </Box>
        </FormProvider>
      </Paper>
    </Box>
  );
}

export default SignUpPage;
