import React, { useState } from 'react';
import { LoginPage } from './pages/login';
import SignUpPage from './pages/signup/SignUp';
import { styled } from '@mui/system';
import useMediaQuery from './services/useMediaQuery';
import { Snackbar, Alert } from '@mui/material';

interface ComponentProps {
  isSlideIn: boolean;
}

const Component = styled('div')<ComponentProps>`
  width: ${props => (props.isSlideIn ? '100%' : '0')};
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: ${props => (props.isSlideIn ? 'visible' : 'hidden')};
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform: ${props => (props.isSlideIn ? 'translateX(0)' : 'translateX(-100%)')};
`;

function App() {
  const { isMobile } = useMediaQuery();
  const [isSignUp, setIsSignUp] = useState(false);

  const changeToSignUp = () => {
    setIsSignUp(true);
  };

  const changeToLogin = () => {
    setIsSignUp(false);
  };

  const [openNoti, setOpenNoti] = React.useState(false);

  const onSuccessSignUp = () => {
    setOpenNoti(true);
    setIsSignUp(false);
  };

  const handleClose = (event?: React.SyntheticEvent | Event, reason?: string) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpenNoti(false);
  };

  return (
    <>
      {isMobile && (
        <Component isSlideIn={isSignUp}>
          <SignUpPage toLogin={changeToLogin} onSuccess={onSuccessSignUp} />
        </Component>
      )}

      <Component isSlideIn={!isSignUp}>
        <LoginPage toSignUp={changeToSignUp} />
      </Component>
      <Snackbar
        open={openNoti}
        autoHideDuration={6000}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
          Sign up successful! Please log in
        </Alert>
      </Snackbar>
    </>
  );
}

export default App;
