import { styled } from '@mui/material/styles';

interface MeterProps {
  color: string;
  isChecked: boolean;
}

export const Meter = styled('div')<MeterProps>(({ color, isChecked, theme }) => {
  return {
    backgroundColor: isChecked ? color : theme.palette.divider,
    width: 24,
    height: 2,
  };
});
