import { Checkbox, FormHelperText, Grid, TextFieldProps, Typography } from '@mui/material';
import { FieldPath, FieldValues, PathValue, useController } from 'react-hook-form';
import { BaseCustomControllerProps } from '@/common/types';
import { ReactElement } from 'react';

interface CheckBoxControllerProps<
  TFieldValues extends FieldValues = FieldValues,
  TName extends FieldPath<TFieldValues> = FieldPath<TFieldValues>
> extends BaseCustomControllerProps<TFieldValues, TName> {
  label?: ReactElement | string;
  required?: boolean;
  isError?: boolean;
  type?: string;
  disabled?: boolean;
}

const CheckBoxController = <
  TFieldValues extends FieldValues = FieldValues,
  TName extends FieldPath<TFieldValues> = FieldPath<TFieldValues>
>({
  label,
  name,
  type,
  required,
  disabled,
  isError,
  defaultValue,
  control,
  rules,
  ...props
}: CheckBoxControllerProps<TFieldValues, TName> & TextFieldProps) => {
  const {
    field,
    fieldState: { error },
  } = useController<TFieldValues, TName>({
    name,
    control,
    defaultValue,
    rules: {
      required: { message: `This is required`, value: !!required },
      ...rules,
    },
  });

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>, checked: boolean) => {
    field.onChange(checked as PathValue<TFieldValues, TName>);
  };
  return (
    <div>
      <Grid container spacing={1} wrap="nowrap" alignItems="center">
        <Grid item>
          <Checkbox
            onChange={handleChange}
            sx={{
              padding: 0,
            }}
            checked={field.value}
          />
        </Grid>
        <Grid item>
          <Typography variant="small2">{label}</Typography>
        </Grid>
      </Grid>
      {error && <FormHelperText error>{error.message}</FormHelperText>}
    </div>
  );
};

export default CheckBoxController;
