import { Grid, Typography } from '@mui/material';
import React from 'react';
import { Meter } from './PasswordChecker.style';
import { useFormContext } from 'react-hook-form';
import { SignUpRequest } from '@/@types';
import { passwordPointChecker } from '@/helpers/ultis';

const PasswordChecker = () => {
  const { watch } = useFormContext<SignUpRequest>();

  const { label, points, color } = passwordPointChecker(watch('password'));
  return (
    <div>
      <Typography variant="smaller1" component="p" textAlign="end" mb={1} color={color}>
        {label}
      </Typography>
      <Grid container width={110} justifyContent="space-between">
        <Grid item>
          <Meter color={color} isChecked={points === 5} />
        </Grid>
        <Grid item>
          <Meter color={color} isChecked={points >= 4} />
        </Grid>
        <Grid item>
          <Meter color={color} isChecked={points >= 3} />
        </Grid>
        <Grid item>
          <Meter color={color} isChecked />
        </Grid>
      </Grid>
    </div>
  );
};

export default PasswordChecker;
