import { Box, FormHelperText, TextField, TextFieldProps, Typography } from '@mui/material';
import { FieldPath, FieldValues, useController } from 'react-hook-form';
import { RequiredAsterisk } from '@/components/RequiredAsterisk';
import { REGEXP_EMAIL } from '@/helpers/constants';
import { BaseCustomControllerProps } from '@/common/types';

interface TextInputControllerProps<
  TFieldValues extends FieldValues = FieldValues,
  TName extends FieldPath<TFieldValues> = FieldPath<TFieldValues>
> extends BaseCustomControllerProps<TFieldValues, TName> {
  label?: string;
  hideLabel?: boolean;
  fullWidth?: boolean;
  required?: boolean;
  isError?: boolean;
  type?: string;
  disabled?: boolean;
  multiline?: boolean;
  maxRow?: number;
  rows?: number;
}

const TextInputController = <
  TFieldValues extends FieldValues = FieldValues,
  TName extends FieldPath<TFieldValues> = FieldPath<TFieldValues>
>({
  label,
  name,
  type,
  required,
  disabled,
  isError,
  multiline,
  rows,
  maxRow,
  defaultValue,
  control,
  rules,
  hideLabel,
  ...props
}: TextInputControllerProps<TFieldValues, TName> & TextFieldProps) => {
  const {
    field,
    fieldState: { error },
  } = useController<TFieldValues, TName>({
    name,
    control,
    defaultValue,
    rules: {
      required: { message: `${label || 'This field'} is required`, value: !!required },
      ...rules,
    },
  });

  if (rules?.email) {
    rules.pattern = REGEXP_EMAIL;
  }

  if (error?.type === 'pattern' && rules?.email) {
    error.type = 'email';
    error.message = 'Please enter valid email address.';
  }

  return (
    <div>
      {!hideLabel && label && (
        <Box mb={1}>
          <Typography variant="label2" color="system.sub">
            {label} {required && <RequiredAsterisk />}
          </Typography>
        </Box>
      )}
      <TextField
        {...field}
        multiline={multiline}
        rows={rows || 5}
        {...props}
        disabled={disabled}
        error={!!error}
        type={type}
        size="small"
      />
      {error && <FormHelperText error>{error.message}</FormHelperText>}
    </div>
  );
};

export default TextInputController;
