import React, { useState } from 'react';
import { Grid, IconButton, InputAdornment, Typography } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import useMediaQuery from '@/services/useMediaQuery';
import { useFormContext } from 'react-hook-form';
import TextInputController from '@/components/TextField/TextInputController/TextInputController';
import { RequiredAsterisk } from '@/components/RequiredAsterisk';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { SignUpRequest } from '@/@types';
import { validatePassword } from '@/helpers/ultis';
import { CheckBoxController } from '@/components/CheckBox';
import { PasswordChecker } from './PasswordChecker';

interface SignUpFormProps {
  onSignUp: (data: SignUpRequest) => void;
  toLogin: () => void;
  isLoading: boolean;
}

const SignUpForm = ({ onSignUp, toLogin, isLoading }: SignUpFormProps) => {
  const { isMobile } = useMediaQuery();

  const [showPassword, setShowPassword] = useState(false);

  const handleTogglePassword = () => {
    setShowPassword(prevShowPassword => !prevShowPassword);
  };

  const { control, handleSubmit, watch } = useFormContext<SignUpRequest>();

  const onSubmit = (data: SignUpRequest) => {
    onSignUp(data);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <Grid spacing={6} container>
        <Grid item xs={12}>
          <Typography
            variant={isMobile ? 'h4' : 'h3'}
            sx={{
              mb: 2,
            }}
          >
            Sign up
          </Typography>
          <Grid container spacing={2}>
            <Grid item>
              <Typography variant="body2">Already have an account?</Typography>
            </Grid>
            <Grid item>
              <Typography
                variant="body2"
                color="system.link"
                sx={{
                  cursor: 'pointer',
                }}
                onClick={toLogin}
              >
                Sign in
              </Typography>
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <TextInputController
            control={control}
            name="email"
            size="small"
            label="Email"
            required
            fullWidth
            rules={{
              email: true,
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <div>
            <Grid container justifyContent="space-between" mb={1}>
              <Grid item>
                <Typography variant="label2" color="system.sub">
                  Password <RequiredAsterisk />
                </Typography>
              </Grid>
              <Grid item>
                <PasswordChecker />
              </Grid>
            </Grid>
            <TextInputController
              control={control}
              name="password"
              label="Password"
              hideLabel
              size="small"
              required
              fullWidth
              type={showPassword ? 'text' : 'password'}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={handleTogglePassword}>
                      {showPassword ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              rules={{
                validate: validatePassword,
              }}
            />
          </div>
        </Grid>
        <Grid item xs={12}>
          <CheckBoxController
            name="agreement"
            control={control}
            label={
              <Typography variant="small2" color="system.sub">
                I agree to the&nbsp;
                <Typography
                  color="system.link"
                  display="inline"
                  variant="small2"
                  sx={{
                    cursor: 'pointer',
                  }}
                >
                  Terms of Use
                </Typography>
                &nbsp;and&nbsp;
                <Typography
                  color="system.link"
                  display="inline"
                  variant="small2"
                  sx={{
                    cursor: 'pointer',
                  }}
                >
                  Privacy policy.
                </Typography>
              </Typography>
            }
          />
        </Grid>
        <Grid item xs={12}>
          <LoadingButton
            variant="contained"
            sx={{
              padding: 3,
            }}
            type="submit"
            fullWidth
            disabled={!watch('agreement')}
            loading={isLoading}
          >
            <Typography variant="body2">Get started</Typography>
          </LoadingButton>
        </Grid>
      </Grid>
    </form>
  );
};

export default SignUpForm;
